import React, { Component } from 'react'
import "./TopBar.css"
import { Link } from "react-router-dom"

export default class TopBar extends Component {
    constructor(props) {
        super(props)

        this.state = {

        }
        this.bold = {
            "background-color": "rgb(255,255,255,.2)"
        };
        this.homeStyle = this.props.active === "home" ? this.bold : null
        this.contactStyle = this.props.active === "contact" ? this.bold : null;
        this.projectsStyle = this.props.active === "projects" ? this.bold : null;
    }
    render() {
        return (
            <nav className="top_links transparent" id="sidebar">
                <div className="outer" style={this.homeStyle}><div className="inner">
                    <Link to="/">Home</Link>
                </div></div>
                <div className="outer" style={this.contactStyle}><div className="inner">
                    <Link to="/Contact">Contact</Link>
                </div></div>
                <div className="outer" style={this.projectsStyle}><div className="inner">
                    <Link to="/Projects">Projects</Link>
                </div></div>
            </nav>
        )
    }
}

